<div class="container">
    <div class="shoplist-container">
        {#each itemname as {name},i }
            <div class="shop-col">
                <h2>{ name }</h2>
            </div>
        {/each}
    </div>
</div>
<script>
    let itemname = [
        { name: 'Orange'},
        { name: 'Apple'},
        { name: 'Ananas'},
        { name: 'Olive'},
        { name: 'Cool'},
        { name: 'Glace'}
    ]
</script>
<style>
    .shoplist-container {
        display: flex;
        flex-wrap: wrap;
    }
    .shop-col {
        width: 30%;
        margin: 0.5em;
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
        transition: 0.3s;
    }
</style>