<script>
  import { Link } from "svelte-routing";
  let mobilmenuon = { on: false};

  function openmobmenu() {
    mobilmenuon.on = !mobilmenuon.on;
  }
</script>

<style>
  nav {
    background-color: #1a237e;
    color: white;
  }
  .mobile-button {
    visibility: hidden;
  }
  .flex-container {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 92%;
  }
  .nav-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .log-style {
  }
  .list-nav {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    margin: 0;
  }
  .list-items-nav {
    margin: 10px;
    font-weight: bold;
    font-size: 1.3rem;
  }
  .hiden-mob {
    visibility: visible;
  }
  .button-mob {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
  }
  #attentionGrabber {
    display: none;
  }
  .show-shopping-cart {
    margin-right: 10px;
  }
  @media only screen and (max-width: 600px) {
    nav {
      background-color: lightblue;
    }
    .mobile-button {
      visibility: visible;
    }
    .hiden-mob {
      visibility: hidden;
    }
    #attentionGrabber {
      display: block;
    }
    .show-shopping-cart {
      visibility: visible;
    }
    .list-nav-mob {
      display: flex;
      list-style-type: none;
      padding: 0;
      justify-content: space-between;
      margin: 0;
    }
