<nav>
  <div class="flex-container">
    <div class="nav-header">
      <div>
        <Link to="/">
          <span class="log-style">Logo Lether</span>
        </Link>
      </div>
      <div>
        <ul class="list-nav hiden-mob">
          <Link to="/">
            <li class="list-items-nav">Home</li>
          </Link>
          <Link to="/shop">
            <li class="list-items-nav">Shop</li>
          </Link>
          <Link to="/about">
            <li class="list-items-nav">About</li>
          </Link>
          <button class="show-shopping-cart button-mob">
